<template>
  <v-container
    style="overflow-y: scroll; height: 100vh; padding-bottom: 64px;"
    class="no-scrollbar"
  >
    <v-snackbar
      v-model="snackbar"
      :timeout="3000"
      color="success"
    >
      {{ $t('common.saved') }}
      <v-btn
        text
        color="white"
        @click="snackbar = false"
      >
        {{ $t('common.close') }}
      </v-btn>
    </v-snackbar>
    <v-row>
      <v-col
        cols="12"
        md="6"
        offset-md="3"
        xl="4"
        offset-xl="4"
      >
        <v-card
          outlined
        >
          <v-card-title>
            {{ $t('settings.exportCosts.exportCostsBinom') }}

            <!-- ПОДСКАЗКА -->
            <v-icon
              :size="16"
              style="cursor: pointer; margin-left: auto;"
              @click="openLink"
            >
              far fa-question-circle
            </v-icon>
          </v-card-title>
          <v-card-text>
            <!-- ССЫЛКА НА ТРЕКЕР -->
            <v-text-field
              :value="settings.costs.costs_binom_url"
              :label="$t('settings.exportCosts.trackerUrl')"
              outlined
              dense
              hide-details
              class="mb-2"
              @input="checkBinom($event, 'costs_binom_url')"
            />

            <!-- API Key -->
            <v-text-field
              :value="settings.costs.costs_binom_api_key"
              :label="$t('settings.exportCosts.apiKey')"
              outlined
              dense
              hide-details
              class="mb-2"
              @input="checkBinom($event, 'costs_binom_api_key')"
            />
            <v-alert
              v-show="!result.status"
              text
              color="error"
              class="mb-2 py-2"
            >
              {{ result.message }}
            </v-alert>

            <div class="d-flex align-center mb-2">
              {{ $t('settings.exportCosts.id') }}
              <!-- ОБЪЕКТ -->
              <v-autocomplete
                :value="settings.costs.costs_binom_object"
                :item-text="item => $t(`settings.exportCosts.${item.name}`)"
                :item-value="item => item.name"
                :items="type"
                outlined
                dense
                hide-details
                class="mx-2"
                @change="setOptions('costs_binom_object', $event)"
              />
              <span style="width: 70px;">
                {{ $t('settings.exportCosts.isSetIn') }}
              </span>

              <!-- ТОКЕН -->
              <v-autocomplete
                :value="settings.costs.costs_binom_token"
                :item-text="item => item.name"
                :item-value="item => item.value"
                :items="tokens"
                outlined
                dense
                hide-details
                class="mx-2"
                @change="setOptions('costs_binom_token', $event)"
              />
            </div>
          </v-card-text>

          <v-card-title>
            {{ $t('settings.exportCosts.whereToExportCosts') }}
          </v-card-title>

          <v-card-text>
            <!-- В КАКИЕ КАМПАНИИ ЭКСПОРТИРОВАТЬ -->
            <v-autocomplete
              :value="settings.costs.costs_binom_target"
              :item-text="item => $t(`settings.exportCosts.${item.name}`)"
              :item-value="item => item.value"
              :items="selectedCampaigns"
              outlined
              dense
              hide-details
              class="mb-3"
              @change="setOptions('costs_binom_target', $event)"
            />

            <!-- ИСТОЧНИКИ ТРАФИКА -->
            <v-autocomplete
              v-if="settings.costs.costs_binom_target === 'sources'"
              :label="$t(`settings.exportCosts.trafficSources`)"
              :value="settings.costs.costs_binom_sources"
              :item-text="item => item.name"
              :item-value="item => item.id"
              :items="binomData.sources"
              outlined
              dense
              hide-details
              multiple
              small-chips
              deletable-chips
              class="mb-2"
              @change="setOptions('costs_binom_sources', $event)"
            />

            <!-- ГРУППЫ -->
            <v-autocomplete
              v-if="settings.costs.costs_binom_target === 'groups'"
              :label="$t(`settings.exportCosts.groups`)"
              :value="settings.costs.costs_binom_groups"
              :item-text="item => item.name"
              :item-value="item => item.id"
              :items="binomData.groups"
              outlined
              dense
              hide-details
              multiple
              small-chips
              deletable-chips
              class="mb-2"
              @change="setOptions('costs_binom_groups', $event)"
            />

            <!-- КАМПАНИИ -->
            <v-autocomplete
              v-if="settings.costs.costs_binom_target === 'campaigns'"
              :label="$t(`settings.exportCosts.campaigns`)"
              :value="settings.costs.costs_binom_campaigns"
              :item-text="item => item.name"
              :item-value="item => item.id"
              :items="binomData.campaigns"
              outlined
              dense
              hide-details
              multiple
              small-chips
              deletable-chips
              class="mb-2"
              @change="setOptions('costs_binom_campaigns', $event)"
            />
          </v-card-text>

          <v-card-title>
            {{ $t('settings.exportCosts.statPeriod') }}
          </v-card-title>

          <v-card-text>
            <!-- ПЕРИОД СТАТЫ -->
            <v-autocomplete
              :value="settings.costs.costs_binom_period"
              :item-text="item => $t(`settings.exportCosts.${item.name}`)"
              :item-value="item => item.value"
              :items="statPeriod"
              :label="$t(`settings.exportCosts.statPeriod`)"
              outlined
              dense
              hide-details
              class="mb-2"
              @change="setOptions('costs_binom_period', $event)"
            />

            <!-- ИНТЕРВАЛ -->
            <v-autocomplete
              :value="settings.costs.costs_binom_interval"
              :item-text="item => $t(`settings.exportCosts.${item.name}`)"
              :item-value="item => item.value"
              :items="interval"
              :label="$t(`settings.exportCosts.exportFrequency`)"
              outlined
              dense
              hide-details
              class="mb-2"
              @change="setOptions('costs_binom_interval', $event)"
            />

            <!-- ТАЙМАУТ -->
            <v-text-field
              :value="settings.costs.costs_binom_timeout"
              :label="$t('settings.exportCosts.pauseBetweenRequests')"
              :hint="$t('settings.exportCosts.pauseBetweenRequestsHint')"
              :error="touched && $v.settings.costs.costs_binom_timeout.$invalid"
              persistent-hint
              outlined
              dense
              @input="setOptions('costs_binom_timeout', $event)"
            />
          </v-card-text>
          <v-card-actions>
            <v-btn
              text
              color="primary"
              @click="saveBinomCosts"
            >
              {{ $t('common.save') }}
            </v-btn>
          </v-card-actions>
        </v-card>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import interval                                      from '@/constants/settings/interval.js';
import selectedCampaigns                             from '@/constants/settings/selectedCampaigns.js';
import statPeriod                                    from '@/constants/settings/statPeriod.js';
import tokens                                        from '@/constants/settings/tokens.js';
import { maxValue }                                  from 'vuelidate/lib/validators';
import { mapGetters }                                from 'vuex';

export default {
  name: 'SettingsExportCostsBinom',

  data() {
    return {
      snackbar: false,
      type: [
        {
          name: 'ad'
        },
        {
          name: 'adset'
        },
        {
          name: 'campaign'
        }
      ],
      tokens,
      selectedCampaigns,
      statPeriod,
      interval,
      touched: false,
      payloadInput: '',
      result: {
        status: true,
        message: ''
      }
    };
  },

  validations: {
    settings: {
      costs: {
        costs_binom_timeout: {
          maxValue: maxValue(2000)
        }
      }
    }
  },

  computed: {
    ...mapGetters({
      settings: 'settings/settings',
      binomData: 'settings/binomData'
    })
  },

  watch: {
    'settings.costs.costs_binom_timeout': {
      handler () {
        this.touched = false;
      }
    },
  },

  methods: {
    openLink() {
      window.open(`https://docs.dolphin.ru.com/view/8?lang=${this.$vuetify.lang.current}`, '_blank');
    },

    checkBinom(payload, option) {
      this.payloadInput = payload;

      setTimeout(async () => {
        if (this.payloadInput === payload) {
          this.setOptions(option, payload);
          this.result.status = true;
          this.result.message = '';

          let response = null;

          if (this.settings.costs.costs_binom_api_key && this.settings.costs.costs_binom_url) {
            response = await this.api.post('/settings/check_binom', {
              url: this.settings.costs.costs_binom_url,
              api_key: this.settings.costs.costs_binom_api_key
            });
          } else if (this.settings.costs.costs_binom_api_key) {
            response = await this.api.post('/settings/check_binom', {
              api_key: this.settings.costs.costs_binom_api_key
            });
          } else if (this.settings.costs.costs_binom_url) {
            response = await this.api.post('/settings/check_binom', {
              url: this.settings.costs.costs_binom_url
            });
          }

          if (response && response.data && response.data.success === false) {
            this.result.status = false;
            this.result.message = response.data.message;
          }
        }
      }, 500);
    },

    setOptions(option, value) {
      let data = {
        category: 'costs',
        option: option,
        value: value
      };
      this.$store.dispatch('settings/setOptions', data);
    },

    async saveBinomCosts() {
      this.$v.$touch();
      this.touched = true;

      if(!this.$v.$error) {
        const settings = {
          costs: {
            costs_binom_api_key: this.settings.costs.costs_binom_api_key,
            costs_binom_interval: this.settings.costs.costs_binom_interval,
            costs_binom_object: this.settings.costs.costs_binom_object,
            costs_binom_period: this.settings.costs.costs_binom_period,
            costs_binom_token: this.settings.costs.costs_binom_token,
            costs_binom_target: this.settings.costs.costs_binom_target,
            costs_binom_timeout: this.settings.costs.costs_binom_timeout,
            costs_binom_url: this.settings.costs.costs_binom_url,
            costs_binom_sources: this.settings.costs.costs_binom_sources,
            costs_binom_campaigns: this.settings.costs.costs_binom_campaigns,
            costs_binom_groups: this.settings.costs.costs_binom_groups,

          }
        };
        await this.$store.dispatch('settings/saveSettings', settings);
        this.snackbar = true;
      }
    },
  }
};
</script>
