<template>
  <v-container
    style="overflow-y: scroll; height: 100vh; padding-bottom: 64px;"
    class="no-scrollbar"
  >
    <!-- ОПИСАНИЕ ПАУЗЫ ПЕРЕД СМЕНОЙ -->
    <v-dialog
      :value="help.waitBefore"
      width="500"
    >
      <v-card>
        <v-card-title class="headline">
          {{ $t('settings.facebookApi.mobileProxy.help.before.title') }}
        </v-card-title>

        <v-card-text v-html="$t('settings.facebookApi.mobileProxy.help.before.text')" />

        <v-divider />

        <v-card-actions>
          <v-spacer />
          <v-btn
            color="primary"
            text
            @click="help.waitBefore = false"
          >
            {{ $t('common.close') }}
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

    <!-- ОПИСАНИЕ ПАУЗЫ ПОСЛЕ СМЕНЫ -->
    <v-dialog
      :value="help.waitAfter"
      width="500"
    >
      <v-card>
        <v-card-title class="headline">
          {{ $t('settings.facebookApi.mobileProxy.help.after.title') }}
        </v-card-title>

        <v-card-text v-html="$t('settings.facebookApi.mobileProxy.help.after.text')" />

        <v-divider />

        <v-card-actions>
          <v-spacer />
          <v-btn
            color="primary"
            text
            @click="help.waitAfter = false"
          >
            {{ $t('common.close') }}
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

    <v-snackbar
      v-model="snackbar"
      :timeout="3000"
      color="success"
    >
      {{ $t('common.saved') }}
      <v-btn
        text
        color="white"
        @click="snackbar = false"
      >
        {{ $t('common.close') }}
      </v-btn>
    </v-snackbar>
    <v-row>
      <v-col
        cols="12"
        md="6"
      >
        <v-card
          outlined
        >
          <v-overlay
            v-if="profile.role_id !== 1 && globals.state"
            absolute
            :value="true"
            :opacity="0.9"
            style="margin-left: 56px;"
          >
            <span v-html="$t('settings.usingGlobals', {admin: globals.admin_display_name || globals.admin_login})" />
          </v-overlay>
          <v-card-title>
            {{ $t('settings.facebookApi.dataUpdate.title') }}
          </v-card-title>

          <!-- ОБНОВЛЕНИЕ ДАННЫХ -->
          <v-card-text>
            <div>
              <v-switch
                v-model="fbSettings.sync"
                :label="$t('settings.facebookApi.dataUpdate.sync')"
                dense
                class="my-0 py-0"
              />
            </div>

            <!-- ОБНОВЛЯТЬ ДАННЫЕ АККАУНТОВ -->
            <div
              v-if="fbSettings.sync"
              class="mb-5"
            >
              {{ $t('common.every') }}
              <input
                v-model="fbSettings.interval"
                type="text"
                style="border: 1px solid; width: 50px; text-align: center; padding: 0; border-radius: 5px;"
              >
              {{ $t('common.min').toLowerCase() }}
            </div>
            
            <!-- АВТОПУБЛИКАЦИЯ ФП -->
            <div>
              <v-switch
                v-model="fbSettings.auto_publish"
                :label="$t('settings.facebookApi.dataUpdate.autoPublishOfFanPages')"
                dense
                class="my-0 py-0"
              />
            </div>

            <!-- АВТОПУБЛИКАЦИЯ ФП АККАУНТОВ -->
            <!-- <div
              v-if="fbSettings.auto_publish"
              class="mb-5"
            >
              {{ $t('common.every') }}
              <input
                v-model="settings.intervals.fp_auto_publish_time"
                type="text"
                style="border: 1px solid; width: 50px; text-align: center; padding: 0; border-radius: 5px;"
              >
              {{ $t('common.min').toLowerCase() }}
            </div> -->
          </v-card-text>

          <v-card-actions>
            <v-btn
              text
              color="primary"
              :loading="loading.dataUpdate"
              @click="saveDataUpdate"
            >
              {{ $t('common.save') }}
            </v-btn>
          </v-card-actions>
        </v-card>
      </v-col>

      <v-col
        cols="12"
        md="6"
      >
        <!-- МОБИЛЬНЫЕ ПРОКСИ -->
        <v-card
          outlined
        >
          <v-overlay
            v-if="profile.role_id !== 1 && globals.state"
            absolute
            :value="true"
            :opacity="0.9"
            style="margin-left: 56px;"
          >
            <span v-html="$t('settings.usingGlobals', {admin: globals.admin_display_name || globals.admin_login})" />
          </v-overlay>
          
          <v-card-title>
            {{ $t('settings.facebookApi.mobileProxy.title') }}
          </v-card-title>
          
          <v-card-text>
            <!-- МЕНЯТЬ IP В ИНТЕРФЕЙСЕ -->
            <div>
              <v-switch
                v-model="settings.general.change_ip_from_ui"
                dense
                class="my-0 py-0"
              >
                <template #label>
                  <span>
                    {{ $t('settings.facebookApi.mobileProxy.ui') }}
                  </span>
                </template>
              </v-switch>
            </div>
            <!-- ЖДАТЬ ПЕРЕД СМЕНОЙ -->
            <div>
              <input
                v-model="settings.general.general_wait_before_change_ip"
                type="text"
                style="border: 1px solid; width: 50px; text-align: center; padding: 0; border-radius: 5px; margin-right: 8px;"
              >
              {{ $t('settings.facebookApi.mobileProxy.waitBefore') }}
              <v-icon
                class="ml-1"
                :size="16"
                style="margin-top: -3px; cursor: pointer;"
                @click="help.waitBefore = true;"
              >
                far fa-question-circle
              </v-icon>
            </div>
            <div
              v-if="settings.general.general_wait_before_change_ip < 30"
              class="mt-3"
            >
              <v-alert
                text
                color="orange"
                style="font-size: 12px; padding: 8px;"
              >
                {{ $t('settings.facebookApi.mobileProxy.warning') }}
              </v-alert>
            </div>
            <div />
            <!-- ЖДАТЬ ПОСЛЕ СМЕНЫ -->
            <div class="mt-3">
              <input
                v-model="settings.general.general_wait_proxy_change_ip"
                type="text"
                style="border: 1px solid; width: 50px; text-align: center; padding: 0; border-radius: 5px; margin-right: 8px;"
              >
              {{ $t('settings.facebookApi.mobileProxy.wait') }}
              <v-icon
                class="ml-1"
                :size="16"
                style="margin-top: -3px; cursor: pointer;"
                @click="help.waitAfter = true;"
              >
                far fa-question-circle
              </v-icon>
            </div>
            <div
              v-if="settings.general.general_wait_proxy_change_ip < 15"
              class="mt-3"
            >
              <v-alert
                text
                color="orange"
                style="font-size: 12px; padding: 8px;"
              >
                {{ $t('settings.facebookApi.mobileProxy.warning') }}
              </v-alert>
            </div>
          </v-card-text>

          <v-card-actions>
            <v-btn
              text
              color="primary"
              @click="saveMobProxy"
            >
              {{ $t('common.save') }}
            </v-btn>
          </v-card-actions>
        </v-card>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import {mapGetters} from 'vuex';

export default {
  name: 'SettingsFacebookApi',

  data() {
    return {
      commentsModes: [
        {text: this.$t('settings.facebookApi.comments.download'), value: 'download'},
        {text: this.$t('settings.facebookApi.comments.hide_all'), value: 'hide_all'},
        {text: this.$t('settings.facebookApi.comments.delete_all'), value: 'delete_all'},
      ],
      snackbar: false,
      help: {
        waitBefore: false,
        waitAfter: false,
      },
      loading: {
        dataUpdate: false,
      }
    };
  },

  computed: {
    ...mapGetters({
      profile: 'main/profile',
      globals: 'settings/globals',
      fbSettings: 'settings/fbSettings',
      settings: 'settings/settings'
    })
  },

  watch: {
    // 'fbSettings.auto_publish': {
    //   deep: true,
    //   handler() {
    //     if (!this.fbSettings.auto_publish) {
    //       let data = {
    //         category: 'fbSettings',
    //         option: 'fp_auto_publish_time',
    //         value: 0
    //       };
    //       this.$store.dispatch('settings/setOptions2', data);
    //     }
    //   }
    // },
    
    'fbSettings.sync': {
      deep: true,
      handler() {
        if (!this.fbSettings.sync) {
          let data = {
            category: 'fbSettings',
            option: 'interval',
            value: 0
          };
          this.$store.dispatch('settings/setOptions2', data);
        }
      }
    }
  },

  methods: {
    async saveDataUpdate() {
      this.loading.dataUpdate = true;
      try {
        await this.api.put('/fb_settings', {
          sync: this.fbSettings.sync,
          interval: this.fbSettings.interval,
          auto_publish: this.fbSettings.auto_publish,
        });
      } catch (error) {
        // 
      } finally {
        this.loading.dataUpdate = false;
      }
    },

    async saveMobProxy() {
      const settings = {
        general: {
          change_ip_from_ui: this.settings.general.change_ip_from_ui,
          general_wait_before_change_ip: this.settings.general.general_wait_before_change_ip,
          general_wait_proxy_change_ip: this.settings.general.general_wait_proxy_change_ip,
        }
      };
      await this.$store.dispatch('settings/saveSettings', settings);
      this.snackbar = true;
    }
  },

  async created() {
    await this.$store.dispatch('settings/loadFbSettings');
  }
};
</script>