<template>
  <v-tabs
    centered
  >
    <!-- BINOM -->
    <v-tab>
      BINOM
    </v-tab>
    <v-tab-item>
      <binom />
    </v-tab-item>
    
    <!-- KEITARO -->
    <v-tab>
      KEITARO
    </v-tab>
    <v-tab-item>
      <keitaro />
    </v-tab-item>
  </v-tabs>
</template>

<script>
import {mapGetters}   from 'vuex';

import Binom          from './SettingsExportCostsBinom';
import Keitaro        from './SettingsExportCostsKeitaro';

export default {
  name: 'ExportCosts',

  components: {
    Binom,
    Keitaro
  },

  data() {
    return {
      snackbar: false
    };
  },

  computed: {
    ...mapGetters({
      profile: 'main/profile',
      settings: 'settings/settings',
      globals: 'settings/globals'
    })
  },

  watch: {
    'settings.costs.costs_binom_api_key': {
      deep: true,
      handler() {
        if (this.settings.costs.costs_binom_api_key && this.settings.costs.costs_binom_url) {
          let data = {
            url: this.settings.costs.costs_binom_url,
            api_key: this.settings.costs.costs_binom_api_key
          };
          this.$store.dispatch('settings/setBinomData', data);
        }     
      }
    },

    'settings.costs.costs_binom_url': {
      deep: true,
      handler() {
        if (this.settings.costs.costs_binom_api_key && this.settings.costs.costs_binom_url) {
          let data = {
            url: this.settings.costs.costs_binom_url,
            api_key: this.settings.costs.costs_binom_api_key
          };
          this.$store.dispatch('settings/setBinomData', data);
        }    
      }
    },

    'settings.costs.costs_keitaro_api_key': {
      deep: true,
      handler() {
        if (this.settings.costs.costs_keitaro_api_key && this.settings.costs.costs_keitaro_url) {
          let data = {
            url: this.settings.costs.costs_keitaro_url,
            api_key: this.settings.costs.costs_keitaro_api_key
          };
          this.$store.dispatch('settings/setKeitaroData', data);
        }  
      }
    },

    'settings.costs.costs_keitaro_url': {
      deep: true,
      handler() {
        if (this.settings.costs.costs_keitaro_api_key && this.settings.costs.costs_keitaro_url) {
          let data = {
            url: this.settings.costs.costs_keitaro_url,
            api_key: this.settings.costs.costs_keitaro_api_key
          };
          this.$store.dispatch('settings/setKeitaroData', data);
        } 
      }
    }
  },

  methods: {
    setNotificationOptions(option, value) {
      let data = {
        option: option,
        value: value
      };
      this.$store.dispatch('settings/setNotificationOptions', data);
    }
  }
};
</script>