<script>

export default {
  name: 'SettingsAdsCreationUrlParams',

  props: {
    settings: {
      type: Object,
      default: () => ({})
    }
  },

  data() {
    return {
      snackbar: false
    };
  },

  methods: {
    addParam(text) {
      if (this.settings.upload.url_params.search(text) === -1) {
        this.settings.upload.url_params += text;
      } else {
        this.settings.upload.url_params = this.settings.upload.url_params.replace(text, '');
      }
    },

    pasteAll() {
      let allParams = '&ad_id={{ad.id}}&adset_id={{adset.id}}&campaign_id={{campaign.id}}&cab_id={{cab.id}}&bm_id={{bm.id}}&pixel_id={{pixel.id}}&dolphin_account_name={{dolphin.account.name}}&cab_name={{cab.name}}&bm_name={{bm.name}}&creative_name={{creative.name}}&campaign_name={{campaign.name}}&adset_name={{adset.name}}&ad_name={{ad.name}}';
      if (this.settings.upload.url_params === '') {
        this.settings.upload.url_params = allParams;
      } else {
        this.settings.upload.url_params = '';
      }
    },

    async saveGeneral() {
      let urlParams = this.settings.upload.url_params;

      while (urlParams.startsWith('?')) {
        urlParams = urlParams.substring(1);
      }

      const settings = {
        upload: {
          url_params: urlParams,
          url_params_target: this.settings.upload.url_params_target
        }
      };
      await this.$store.dispatch('settings/saveSettings', settings);
      this.snackbar = true;
    }
  }
};
</script>