<template>
  <v-container
    style="overflow-y: scroll; height: 100vh; padding-bottom: 64px;"
    class="no-scrollbar"
  >
    <v-snackbar
      v-model="snackbar"
      :timeout="3000"
      color="success"
    >
      {{ $t('common.saved') }}
      <v-btn
        text
        color="white"
        @click="snackbar = false"
      >
        {{ $t('common.close') }}
      </v-btn>
    </v-snackbar>
    <v-row>
      <v-col
        cols="12"
        md="6"
      >
        <!-- ОБЩИЕ -->
        <v-card
          outlined
        >
          <v-card-title>
            {{ $t('settings.adsCreation.general') }}
          </v-card-title>
          
          <v-card-text>
            <!-- КОЛИЧЕСТВО ОДНОВРЕМЕННЫХ ЗАДАЧ -->
            <div>
              <input
                :value="settings.upload.max_upload_jobs"
                type="number"
                style="border: 1px solid; width: 50px; text-align: center; padding: 0; border-radius: 5px; margin-right: 8px;"
                @input="settings.upload.max_upload_jobs = Math.round($event.target.value.trim().replace('-', ''))"
              >
              {{ $t('settings.adsCreation.concurrentJobsNumber') }}
            </div>
          </v-card-text>

          <v-card-actions>
            <v-btn
              text
              color="primary"
              @click="saveGeneral"
            >
              {{ $t('common.save') }}
            </v-btn>
          </v-card-actions>
        </v-card>
      </v-col>
      <!-- ПАРАМЕТРЫ ССЫЛКИ -->
      <v-col
        cols="12"
        md="6"
      >
        <url-params
          :settings="settings"
          class="mb-5"
        />
        <deeplink-params :settings="settings" />
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import DeeplinkParams    from '@/components/settings/adCreation/SettingsAdsCreationDeeplinkParams.vue';
import UrlParams         from '@/components/settings/adCreation/SettingsAdsCreationUrlParams.vue';
import {mapGetters}      from 'vuex';

export default {
  name: 'SettingsAdsCreation',

  components: {
    DeeplinkParams,
    UrlParams
  },

  data() {
    return {
      snackbar: false
    };
  },

  computed: {
    ...mapGetters({
      profile: 'main/profile',
      settings: 'settings/settings',
      globals: 'settings/globals'
    })
  },

  methods: {
    async saveGeneral() {
      const settings = {
        upload: {
          max_upload_jobs: this.settings.upload.max_upload_jobs
        }
      };
      await this.$store.dispatch('settings/saveSettings', settings);
      this.snackbar = true;
    }
  },
};
</script>