<template>
  <v-container
    style="overflow-y: scroll; height: 100vh; padding-bottom: 64px;"
    class="no-scrollbar"
  >
    <v-snackbar
      v-model="snackbar"
      :timeout="3000"
      color="success"
    >
      {{ $t('common.saved') }}
      <v-btn
        text
        color="white"
        @click="snackbar = false"
      >
        {{ $t('common.close') }}
      </v-btn>
    </v-snackbar>
    <v-row>
      <!-- ОСНОВНЫЕ -->
      <v-col
        :cols="12"
        :md="6"
      >
        <v-card outlined>
          <v-card-title>
            {{ $t('settings.tabs.general') }}
          </v-card-title>
          
          <v-card-text>
            <v-switch
              v-model="settings.general.send_debug_data"
              :label="$t('settings.general.sendDebugData')"
              dense
              class="my-0 py-0"
            />
          </v-card-text>

          <v-card-actions>
            <v-btn
              text
              color="primary"
              @click="saveGeneral"
            >
              {{ $t('common.save') }}
            </v-btn>
          </v-card-actions>
        </v-card>
      </v-col>

      <!-- ГЛОБАЛЬНЫЕ НАСТРОЙКИ -->
      <v-col
        cols="12"
        md="6"
      >
        <v-card
          outlined
        >
          <v-card-title>
            {{ $t('settings.general.useGlobals.title') }}
          </v-card-title>

          <v-card-text>
            <div v-if="profile.role_id === 1">
              <v-switch
                v-model="settings.general.use_globals"
                :label="$t('settings.general.useGlobals.useGlobals')"
                dense
                class="my-0 py-0"
              />
            </div>
            <!-- <div>
              <v-switch
                v-model="settings.general.send_debug_data"
                :label="$t('settings.general.sendDebugData')"
                dense
                class="my-0 py-0"
              />
            </div> -->
          </v-card-text>

          <v-card-actions>
            <v-btn
              text
              color="primary"
              @click="saveGlobals"
            >
              {{ $t('common.save') }}
            </v-btn>
          </v-card-actions>
        </v-card>
      </v-col>

      <!-- New Features -->
      <v-col
        cols="12"
        md="6"
      >
        <v-card
          outlined
        >
          <v-card-title>
            {{ $t('settings.general.experimentalFeature.title') }}
          </v-card-title>

          <v-card-text>
            <div v-if="profile.role_id === 1">
              <v-switch
                v-model="settings.general.experimental_feature"
                :label="$t('settings.general.experimentalFeature.description')"
                dense
                class="my-0 py-0"
              />
            </div>
          </v-card-text>

          <v-card-actions>
            <v-btn
              text
              color="primary"
              @click="saveExperimentalFeature"
            >
              {{ $t('common.save') }}
            </v-btn>
          </v-card-actions>
        </v-card>
      </v-col>

      <!-- ДОМЕНЫ -->
      <v-col
        :cols="12"
        :md="6"
      >
        <v-card outlined>
          <v-card-title>
            {{ $t('settings.tabs.domains') }}
          </v-card-title>
          
          <v-card-text>
            <!-- CLOUDFLARE API KEY -->
            <v-text-field
              v-model="settings.domains.cf_api_key"
              label="Cloudflare API key"
              outlined
              dense
              hide-details
              class=""
              :spellcheck="false"
            />
            <a
              href="https://docs.dolphin.ru.com/view/43"
              target="_blank"
              style="font-size: 12px;"
            >{{ $t('common.howToGet') }}?</a>

            <!-- CLOUDFLARE EMAIL -->
            <!-- <v-text-field
              v-model="settings.domains.cf_email"
              label="Cloudflare email"
              outlined
              dense
              hide-details
              :spellcheck="false"
            /> -->
          </v-card-text>

          <v-card-actions>
            <v-btn
              text
              color="primary"
              @click="saveDomains"
            >
              {{ $t('common.save') }}
            </v-btn>
          </v-card-actions>
        </v-card>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import { mapFields }     from 'vuex-map-fields';
import {mapGetters}      from 'vuex';

export default {
  name: 'SettingsGeneral',

  data() {
    return {
      snackbar: false
    };
  },

  computed: {
    ...mapGetters({
      profile: 'main/profile',
      globals: 'settings/globals'
    }),

    ...mapFields('settings', ['settings']),
  },

  methods: {
    async saveGeneral() {
      const settings = {
        general: {
          send_debug_data: this.settings.general.send_debug_data
        }
      };
      await this.$store.dispatch('settings/saveSettings', settings);
      // await this.$store.dispatch('main/loadProfile');
      location.reload();
      this.snackbar = true;
    },

    async saveExperimentalFeature() {
      const settings = {
        general: {
          experimental_feature: this.settings.general.experimental_feature
        }
      };
      await this.$store.dispatch('settings/saveSettings', settings);
      this.snackbar = true;
    },

    async saveGlobals() {
      const settings = {
        general: {
          use_globals: this.settings.general.use_globals
        }
      };
      await this.$store.dispatch('settings/saveSettings', settings);
      this.snackbar = true;
    },

    async saveDomains() {
      const settings = {
        domains: {
          cf_api_key: this.settings.domains.cf_api_key,
          // cf_email: this.settings.domains.cf_email
        }
      };
      await this.$store.dispatch('settings/saveSettings', settings);
      this.snackbar = true;
    },
  }
};
</script>