    <v-card-actions>
      <v-btn
        text
        color="primary"
        @click="saveGeneral"
      >
        {{ $t('common.save') }}
      </v-btn>
    </v-card-actions>
  </v-card>
</template>
<script>

export default {
  name: 'SettingsAdsCreationDeeplinkParams',

  props: {
    settings: {
      type: Object,
      default: () => ({})
    }
  },

  data() {
    return {
      snackbar: false
    };
  },

  methods: {
    addParam(text) {
      if (this.settings.upload.deeplink_params.search(text) === -1) {
        this.settings.upload.deeplink_params += text;
      } else {
        this.settings.upload.deeplink_params = this.settings.upload.deeplink_params.replace(text, '');
      }
    },

    pasteAll() {
      let allParams = '&ad_id={{ad.id}}&adset_id={{adset.id}}&campaign_id={{campaign.id}}&cab_id={{cab.id}}&bm_id={{bm.id}}&pixel_id={{pixel.id}}&dolphin_account_name={{dolphin.account.name}}&cab_name={{cab.name}}&bm_name={{bm.name}}&creative_name={{creative.name}}';
      if (this.settings.upload.deeplink_params === '') {
        this.settings.upload.deeplink_params = allParams;
      } else {
        this.settings.upload.deeplink_params = '';
      }
    },

    async saveGeneral() {
      const settings = {
        upload: {
          deeplink_params: this.settings.upload.deeplink_params
        }
      };
      await this.$store.dispatch('settings/saveSettings', settings);
      this.snackbar = true;
    }
  }
};
</script>