<template>
  <v-container
    style="overflow-y: scroll; height: 100vh; padding-bottom: 64px;"
    class="no-scrollbar"
  >
    <v-snackbar
      v-model="snackbar"
      :timeout="3000"
      color="success"
    >
      {{ $t('common.saved') }}
      <v-btn
        text
        color="white"
        @click="snackbar = false"
      >
        {{ $t('common.close') }}
      </v-btn>
    </v-snackbar>
    <v-snackbar
      v-model="snackbarSent"
      :timeout="3000"
      color="success"
    >
      {{ $t('common.sent') }}
      <v-btn
        text
        color="white"
        @click="snackbarSent = false"
      >
        {{ $t('common.close') }}
      </v-btn>
    </v-snackbar>
    <v-row>
      <v-col
        cols="12"
        md="6"
      >
        <v-card
          outlined
        >
          <v-card-title>
            {{ $t('settings.notifications.notifications') }}
          </v-card-title>
          <v-card-text>
            <!--  УВЕДОМЛЯТЬ В ТЕЛЕГРАМ -->
            <v-switch
              v-model="settings.notifications.notifications_telegram"
              :label="$t('settings.notifications.nofityOnTelegram')"
              dense
              hide-details
              class="mt-0"
            />
            <div
              v-if="settings.notifications.notifications_telegram"
            >
              <!-- СЛЕТЫ ТОКЕНОВ -->
              <v-switch
                v-model="settings.notifications.notifications_telegram_tokens"
                :label="$t('settings.notifications.tokenFails')"
                dense
                hide-details
                class="ml-4"
              />

              <!-- СТАТУСЫ КАБИНЕТОВ -->
              <v-switch
                v-model="settings.notifications.notifications_telegram_cabs"
                :label="$t('settings.notifications.cabStatusChanges')"
                dense
                hide-details
                class="ml-4"
              />

              <!-- МОДЕРАЦИЯ ОБЪЯВЛЕНИЙ -->
              <v-switch
                v-model="settings.notifications.notifications_telegram_moderation"
                :label="$t('settings.notifications.moderationEvents')"
                dense
                hide-details
                class="ml-4"
              />

              <!-- КОГДА ДО БИЛЛИНГА ОСТАЕТСЯ -->
              <div class="d-flex align-end ml-4">
                <v-switch
                  v-model="settings.notifications.notifications_telegram_billing"
                  :label="$t('settings.notifications.whenBillingRemains')"
                  dense
                  hide-details
                />
                <input
                  :value="settings.notifications.notifications_telegram_billing_threshold"
                  type="number"
                  style="width: 50px;"
                  class="native-input ml-2"
                  @change="setOptions('notifications_telegram_billing_threshold', parseInt($event.target.value))"
                >
                <span class="ml-1">%</span>
              </div>

              <!-- ИЗМЕНЕНИЕ СТАТУСА ФП -->
              <v-switch
                v-model="settings.notifications.notifications_telegram_fp_status"
                :label="$t('settings.notifications.fpStatus')"
                dense
                hide-details
                class="ml-4 mb-4"
              />

              <!-- ПРИБЛИЖЕНИЕ КОНЦА СРОКА ПРОКСИ -->
              <v-switch
                v-model="settings.notifications.notifications_telegram_proxy_expirity"
                :label="$t('settings.notifications.proxyExpirity')"
                dense
                hide-details
                class="ml-4 mb-4"
              />

              <!-- КЛЮЧ ALARMER BOT -->
              <v-text-field
                :value="settings.notifications.notifications_alarmerbot_key"
                :label="$t('settings.notifications.alarmerBotApiKey')"
                outlined
                dense
                hide-details
                class="mb-4"
                @input="setOptions('notifications_alarmerbot_key', $event)"
              />
              <a
                style="cursor: pointer; color: #2196F3; text-decoration: underline"
                target="_blank"
                href="https://docs.dolphin.ru.com/view/6"
              >
                {{ $t('settings.notifications.manual') }}
              </a>
              <br>
              <span
                style="cursor: pointer; color: #2196F3; text-decoration: underline"
                @click="sendTestMessage"
              >
                {{ $t('settings.notifications.sendTestMessage') }}
              </span>
            </div>
          </v-card-text>
          <v-card-actions>
            <v-btn
              text
              color="primary"
              @click="saveNotifications"
            >
              {{ $t('common.save') }}
            </v-btn>
          </v-card-actions>
        </v-card>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import {mapGetters} from 'vuex';

export default {
  name: 'Notifications',

  data() {
    return {
      snackbar: false,
      snackbarSent: false
    };
  },

  computed: {
    ...mapGetters({
      profile: 'main/profile',
      settings: 'settings/settings',
      globals: 'settings/globals'
    })
  },

  methods: {
    setOptions(option, value) {
      let data = {
        category: 'notifications',
        option: option,
        value: value
      };
      this.$store.dispatch('settings/setOptions', data);
    },

    async saveNotifications() {
      const settings = {
        notifications: {
          notifications_telegram: this.settings.notifications.notifications_telegram,
          notifications_telegram_tokens: this.settings.notifications.notifications_telegram_tokens,
          notifications_telegram_cabs: this.settings.notifications.notifications_telegram_cabs,
          notifications_telegram_moderation: this.settings.notifications.notifications_telegram_moderation,
          notifications_telegram_billing: this.settings.notifications.notifications_telegram_billing,
          notifications_telegram_billing_threshold: this.settings.notifications.notifications_telegram_billing_threshold,
          notifications_telegram_proxy_expirity: this.settings.notifications.notifications_telegram_proxy_expirity,
          notifications_telegram_fp_status: this.settings.notifications.notifications_telegram_fp_status,
          notifications_alarmerbot_key: this.settings.notifications.notifications_alarmerbot_key
        }
      };
      await this.$store.dispatch('settings/saveSettings', settings);
      this.snackbar = true;
    },

    async sendTestMessage () {
      await this.api.get('/settings/telegram_test');
      this.snackbarSent = true;
    }

  }
};
</script>